export default {
  name: 'Share',
  data() {
    return {
      transform: 'translateY(50vh)',
      timer: 0,
      backgroundColor: 'rgba(0,0,0,0)',
      show: false,
      config: {},
    };
  },
  props: {
    contentHeight: {
      type: Number,
      default: 0,
    },
    // 是否是tabbar页面
    hasTabbar: {
      type: Boolean,
      default: false,
    },
    shareList: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  created() {
    const height = `${uni.upx2px(this.contentHeight)}px`;
    this.config = {
      height,
      transform: `translateY(${height})`,
      backgroundColor: 'rgba(0,0,0,.4)',
    };
    this.transform = this.config.transform;
  },
  methods: {
    toggleMask() {
      // 防止高频点击
      if (this.timer == 1) {
        return;
      }
      this.timer = 1;
      setTimeout(() => {
        this.timer = 0;
      }, 500);

      if (this.show) {
        this.transform = this.config.transform;
        this.backgroundColor = 'rgba(0,0,0,0)';
        setTimeout(() => {
          this.show = false;
          this.hasTabbar && uni.showTabBar();
        }, 200);
        return;
      }

      this.show = true;
      // 等待mask重绘完成执行
      if (this.hasTabbar) {
        uni.hideTabBar({
          success: () => {
            setTimeout(() => {
              this.backgroundColor = this.config.backgroundColor;
              this.transform = 'translateY(0px)';
            }, 10);
          },
        });
      } else {
        setTimeout(() => {
          this.backgroundColor = this.config.backgroundColor;
          this.transform = 'translateY(0px)';
        }, 10);
      }
    },
    // 防止冒泡和滚动穿透
    stopPrevent() {},
    // 分享操作
    shareToFriend(type) {
      this.$api.msg(`分享给${type}`);
      this.toggleMask();
    },
  },
};
